/* ==========================================================================
   Hero Area
   ========================================================================== */
.hero-area {
  background: url('../img/intro.png') no-repeat center 65%;
  color: #fff;
  overflow: hidden;
  position: relative;
  background-size: 50%;
    .overlay{
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0px;
      left: 0px;
    background: rgb(85,51,255);
    background: linear-gradient(95deg, rgba(85,51,255,1) 40%, rgba(37,221,245,1) 100%);
        -webkit-transform: skewY(-12deg);
        transform: skewY(-12deg);
        -webkit-transform-origin: 0;
        transform-origin: 0;
        z-index: -12
    }
    .contents{
        padding: 250px 0 280px;
      h2{ 
  color: #fff;
  font-size: 48px;
  font-weight: 600;
  line-height: 60px;
  margin-bottom: 20px;
      }
      p{
  color: #fff;
  font-size: 18px;
  line-height: 26px;
      }
      .btn{
  margin-top: 40px;
  margin-right: 20px;
  text-transform: uppercase;
  padding: 14px 40px;
      }
      .btn-border{
        border: 1px solid #fff;
        color: #fff;
      }
    }

   .intro-img{
      padding: 148px 0 0px;
      img{
        display: block;
        height: auto;
        max-width: 100%;
      }
    }
  }
